<template>
  <div class="col-md-6 col-sm-12">
    <div :class="classes" class="img-wrap justify-content-sm-center">
      <img v-if="url" :src="relativeUrl" :alt="title" :title="title"
           class="img-fluid img-thumbnail">
      <img v-else :src="relativeUrl" :alt="title" :title="title"
           class="img-fluid">
    </div>
  </div>
</template>

<script>
  import Urls from '../../utils/Urls';

  const DEFAULT_IMAGE = '/assets/img/theatre.png';

  export default {
    props: {
      title: {
        type: String,
        required: true
      },
      url: {
        type: String,
        required: true
      },
      side: {
        type: String,
        required: true
      }
    },
    computed: {
      classes() {
        return this.side === 'left' ?
          ['justify-content-md-start'] :
          ['justify-content-md-end']
      },
      relativeUrl() {
        if (this.url) {
          return Urls.relativeUrl(this.url)
        } else {
          return Urls.relativeUrl(DEFAULT_IMAGE)
        }
      }
    }
  }
</script>
