<template>
  <div class="mb-5">
    <div class="d-flex align-content-center">
      <span class="h4 text-primary">
        {{ gallery.title }}
        <span v-if="gallery.author" class="small text-dark">(autor: {{gallery.author}})</span>
      </span>
      <span class="badge badge-pill badge-secondary p-2 m-2 ml-auto align-self-center">
        {{ new Date(gallery.date) | dateFormat('D. M.') }}
      </span>
    </div>

    <div v-images-loaded="loaded" v-gallery class="d-flex no-gutters popup-gallery">
      <div v-for="(column, index) in columns" :key="index"
           :data-column="index"
           class="d-flex flex-column gallery-column">
        <div v-for="photo in column" class="p-1">
          <a class="gallery-box" :href="photo">
            <img class="img-fluid"
                 :ref="photo"
                 :alt="gallery.title"
                 :src="photo">
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import imagesLoaded from 'vue-images-loaded'
  import PhotoGalleryGrid from './PhotoGalleryGrid'
  import Urls from '../../utils/Urls'

  const COLUMNS_NUM = 3;


  const splitColumns = (photos) => {
      const result = {};
      let i = 0;
      do {
        const photo = photos.pop();
        const index = 'c' + (i % COLUMNS_NUM);
        (result[index] || (result[index] = [])).push(Urls.relativeUrl(photo));
        i++
      } while (photos.length);

      return result;
  };

  const gallery = (el) => {
    $(el).magnificPopup({
      delegate: 'a',
      type: 'image',
      tLoading: 'Loading image #%curr%...',
      mainClass: 'mfp-img-mobile',
      gallery: {
        enabled: true,
        navigateByImgClick: true,
        preload: [0, 1]
      },
      image: {
        tError: '<a href="%url%">The image #%curr%</a> could not be loaded.'
      }
    })
  };

  export default {
    props: {
      gallery: {
        type: Object,
        required: true
      }
    },
    data() {
      return {
        columns: splitColumns(this.gallery.photos.slice()),
        gridAdjusted: false
      }
    },
    methods: {
      loaded() {
        if (!this.gridAdjusted) {
          this.gridAdjusted = true;

          const grid = PhotoGalleryGrid.create(this.$refs).adjustColumns();
          const columns = grid.columnImages;

          this.columns = columns
        }
      }
    },
    directives: {
      imagesLoaded,
      gallery
    }
  }
</script>
